import React, { useState } from "react";
import { useDispatch , useSelector } from 'react-redux';

import { tokensSelector , buyTokens } from "../../../slices/filter";

import { Container, Row, Col } from "reusecore/Layout";
import Box from "reusecore/Box";
import Heading from "reusecore/Heading";
import Text from "reusecore/Text";
import Button from "reusecore/Button";
import Input from "reusecore/Form/Input";
import { SectionTitle } from "reusecore/SectionTitle";

import SubscribeWrapper from "./subscribe.style";

const TokensaleDlg = () => {

  const dispatch = useDispatch();
  const tokens = useSelector(tokensSelector);
  const [tokenNum, setTokenNum] = useState(0);

  function fnBuyToken (){
    // start buying token functions from here with the number of token
    dispatch(buyTokens(tokenNum));
  }

  return (
    <SubscribeWrapper id="contact" style={{ marginTop : 150 , paddingBottom : 0}}>
      <Container>
        <Row>
          <Col className="col-12">
            <Box className="subscribe-box-bg">
              <Row>
                <Col className="lg-6 offset-lg-3 xs-12">
                  <SectionTitle>
                    <Heading >
                    <div class="waviy">
                      <div>
                        <span style={{"--i":1}}>T</span>
                        <span style={{"--i":2}}>U</span>
                        <span style={{"--i":3}}>I</span>
                        <span style={{"--i":4}}>T</span>
                        <span style={{"--i":5}}>I</span>
                        <span style={{"--i":6}}>O</span>
                        <span style={{"--i":7}}>N</span>
                      </div>
                      <span style={{ marginLeft : 10 }}> </span>
                      <span style={{"--i":8}}>T</span>
                      <span style={{"--i":8}}>O</span>
                      <span style={{"--i":8}}>K</span>
                      <span style={{"--i":8}}>E</span>
                      <span style={{"--i":8}}>N</span>
                      <span style={{ marginLeft : 10 }}> </span>
                      <span style={{"--i":9}}>I</span>
                      <span style={{"--i":9}}>C</span>
                      <span style={{"--i":9}}>O</span>
                      <span style={{ marginLeft : 10 }}> </span>
                      <span style={{"--i":10}}>S</span>
                      <span style={{"--i":10}}>A</span>
                      <span style={{"--i":10}}>L</span>
                      <span style={{"--i":10}}>E</span>
                    </div>
                    </Heading>
                    <Text>
                      <span class="TokenIcoText" >Your current number of TUITION tokens is </span> {tokens.balance}
                    </Text>
                  </SectionTitle>
                </Col>
              </Row>
              <Row>
                <Col className="lg-8 offset-lg-2 xs-12">
                  <Box className="form-box">
                    <Input
                      type="number"
                      placeholder="type the number of tokens buying... "
                      onChange={(e) => setTokenNum(e.target.value)}
                    />
                    <Button onClick={() => fnBuyToken()}>Buy Tokens</Button>
                  </Box>
                </Col>
              </Row>
            </Box>
          </Col>
        </Row>
      </Container>
    </SubscribeWrapper>
  );
};

export default TokensaleDlg;
